/* active state */
.login-input:focus ~ .bar:before,
.login-input:focus ~ .bar:after {
    width: 50%;
}

.highlight {
    position: absolute;
    height: 60%;
    width: 100px;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
}

.login-input:focus ~ .highlight {
